*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}
a:hover {
  cursor: pointer;
}

img, picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

textarea {
  resize: none;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

button {
  border: none;
}
button:hover {
  cursor: pointer;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content_centered {
  max-width: 80rem;
  margin: 0 auto;
}

.no_select {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none;
}

.align_right {
  display: flex;
  justify-content: flex-end;
}

.align_left {
  display: flex;
  justify-content: flex-start;
}

.align_center {
  display: flex;
  justify-content: center;
}

.text_justified {
  text-align: justify;
}

.text_centered {
  text-align: center;
}

.text_uppercase {
  text-transform: uppercase;
}

.text_capitalize {
  text-transform: capitalize;
}

.w_100 {
  width: 100%;
}

/* Device sizes */
/* Grid */
/* Buttons */
.header {
  background-color: #333333;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.webp .homepage {
  background-image: url(/build/img/header.webp);
}

.no-webp .homepage {
  background-image: url(/build/img/header.jpg);
}

.homepage {
  height: 70rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding-top: 4rem;
}
.homepage .header_content {
  height: 60rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header_content {
  padding-top: 2rem;
}
.header_content .nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.header_content h1 {
  width: 50rem;
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
}

nav {
  display: flex;
  column-gap: 2rem;
}
nav a {
  color: #FFFFFF;
  font-size: 2.2rem;
}
nav a:hover {
  color: #71B100;
}

.dark-mode_logo {
  display: flex;
  justify-content: flex-end;
}
.dark-mode_logo img {
  width: 4rem;
  filter: invert(100%);
}
.dark-mode_logo:hover {
  cursor: pointer;
}

.footer {
  background-color: #333333;
  padding-top: 3rem;
  padding-bottom: 3rem;
}
.footer p {
  color: #FFFFFF;
}

/* Details section */
.details h2 {
  font-weight: 300;
}

.details_icons p {
  text-align: center;
}
.details_icons h3 {
  text-transform: uppercase;
}
@media (min-width: 992px) {
  .details_icons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
.details_icons .details_icon img {
  height: 10rem;
}

/* Ads section */
@media (min-width: 768px) {
  .ads {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .ads {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.ads .ad {
  background-color: #e1e1e1;
  border: 1px solid #d4d4d4;
  margin-bottom: 2rem;
}

.ad_content {
  padding: 1rem 2rem;
}
.ad_content h3 {
  margin-top: 1rem;
  margin-bottom: 0;
}
.ad_content p {
  margin-top: 1rem;
}
.ad_content .price {
  font-size: 3rem;
  font-weight: 700;
  color: #71B100;
}
.ad_content .icons {
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  justify-content: space-between;
}
.ad_content .icons .icon {
  display: flex;
  column-gap: 1rem;
  font-weight: 700;
}
.ad_content a {
  text-transform: uppercase;
  margin-top: 2rem;
}

.property-listing .align_right {
  margin-top: 2rem;
  margin-bottom: 4rem;
  text-transform: uppercase;
}

/* Contact Image section */
.webp .contact-image {
  background-image: url(/build/img/encuentra.webp);
}

.no-webp .contact-image {
  background-image: url(/build/img/encuentra.jpg);
}

.contact-image {
  height: 40rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 4rem;
}
.contact-image h2 {
  font-weight: 700;
}
.contact-image h2, .contact-image p {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
}
.contact-image a {
  text-transform: uppercase;
}

/* Lower section: our blog and testimonial */
.lower-section {
  margin-bottom: 4rem;
}
@media (min-width: 992px) {
  .lower-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.testimonial {
  background-color: #71B100;
  border-radius: 5%;
  padding: 2rem 2rem 2rem 5rem;
}
.testimonial blockquote, .testimonial p {
  font-size: 2.4rem;
  color: #FFFFFF;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(/build/img/comilla.svg);
  width: 5rem;
  height: 5rem;
  display: block;
  position: absolute;
  left: -5rem;
}
@media (min-width: 992px) {
  .testimonial blockquote, .testimonial p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1200px) {
  .testimonial blockquote, .testimonial p {
    font-size: 2.4rem;
  }
}

.post h4 {
  color: #000000;
}
@media (min-width: 768px) {
  .post {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    column-gap: 2rem;
    margin-bottom: 2rem;
  }
  .post img {
    height: auto;
  }
}

p span {
  color: #E08709;
  font-weight: 700;
}

.post_information h4 {
  margin: 0;
  font-weight: 700;
  text-align: left;
}
.post_information h4::after {
  content: "";
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  display: block;
}
@media (min-width: 992px) {
  .post_information p, .post_information span {
    font-size: 1.5rem;
  }
}
@media (min-width: 1200px) {
  .post_information p, .post_information span {
    font-size: 2rem;
  }
}

.button-yellow-inline {
  background-color: #E08709;
  display: inline-block;
  font-size: 1.9rem;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 2rem 1rem 2rem;
}

.button-yellow-block {
  background-color: #E08709;
  display: block;
  font-size: 1.9rem;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 2rem 1rem 2rem;
}

.button-green-inline {
  background-color: #71B100;
  display: inline-block;
  font-size: 1.9rem;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 2rem 1rem 2rem;
}

.button-green-block {
  background-color: #71B100;
  display: block;
  font-size: 1.9rem;
  font-weight: 700;
  text-align: center;
  color: #FFFFFF;
  padding: 1rem 2rem 1rem 2rem;
}

.about_us {
  margin-top: 4rem;
  margin-bottom: 2rem;
}
.about_us img {
  object-fit: cover;
}

@media (min-width: 992px) {
  .about_us {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
  .about_us div h4 {
    text-align: left;
    font-weight: 700;
    margin: 0;
    font-size: 2.2rem;
  }
  .about_us div p {
    margin-top: 2rem;
    font-size: 1.8rem;
  }
  .about_us img {
    max-height: 40rem;
  }
}
main.property {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
main.property h2 {
  margin-top: -1rem;
}
main.property p {
  text-align: justify;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
main.property .property_image {
  height: 40rem;
  object-fit: cover;
}
main.property .price {
  color: #71B100;
  font-weight: 700;
  font-size: 4rem;
}
main.property .icons, main.property .icon {
  display: flex;
}
main.property .icons {
  column-gap: 3rem;
}
main.property .icon {
  column-gap: 2rem;
}
main.property .property_image, main.property .price, main.property .icons, main.property p {
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  main.property .property_image {
    height: auto;
    max-height: 85rem;
  }
}
.contact {
  padding-bottom: 4rem;
}
.contact img {
  height: auto;
}
.contact button {
  text-transform: uppercase;
}

fieldset {
  margin-bottom: 2rem;
}
fieldset legend {
  font-size: 2rem;
}
fieldset .form_field {
  margin-bottom: 2rem;
}
fieldset .form_field:last-of-type {
  margin-bottom: 0;
}

.form_field {
  display: block;
}
.form_field label {
  display: block;
  text-transform: uppercase;
  font-weight: 900;
}
.form_field input, .form_field select, .form_field textarea {
  border: 1px solid #e1e1e1;
}
.form_field input:not([type=radio]), .form_field textarea, .form_field select {
  width: 100%;
  border-radius: 1rem;
  padding: 0.8rem;
}
.form_field textarea {
  height: 20rem;
}
.form_field span {
  display: block;
}
.form_field label.inline {
  display: inline-block;
  margin-right: 1rem;
}
.form_field label.inline:last-of-type {
  margin-left: 4rem;
}